//引入Vue核心库
import Vue from "vue";
//引入Vuex
import Vuex from "vuex";
// 引入axios
import axios from "axios";
axios.defaults.baseURL =
  "https://www.fastmock.site/mock/6883cf2d528955587033eb1b8983b651/blog";
//应用Vuex插件
Vue.use(Vuex);
Vue.use(axios);

//准备actions对象——响应组件中用户的动作
const actions = {
  // 获取文章列表并保存
  saveList(context) {
    axios
      .get("/api/lists")
      .then((res) => {
        // console.log(res);
        context.commit("SAVELIST", res.data.lists);
      })
      .catch((err) => {
        console.log(err);
      });
  },
  // 根据Id获取文章内容
  getArticle(context,value) {
    axios
      .get("/api/lists")
      .then((res) => {
        const list=res.data.lists.filter((item)=>{
         return item.id == value
        })
        console.log(list[0].content);
        context.commit("GETARTICLE", list[0].content);
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
//准备mutations对象——修改state中的数据
const mutations = {
  SAVELIST(state, lists) {
    state.lists = lists;
  },
  GETARTICLE(state, list) {
    state.list = list;
  },
};
//准备state对象——保存具体的数据
const state = {
  lists: [],
  list: {},
};

//创建并暴露store
export default new Vuex.Store({
  actions,
  mutations,
  state,
});
